.trainers {
  padding-top: 122px;
  padding-bottom: 120px;

  @include vp-1199 {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}

.trainers__title {
  margin-bottom: 50px;

  @include vp-1199 {
    text-align: center;
    margin-bottom: 40px;
  }

  @include vp-767 {
    margin-bottom: 42px;
  }
}

.trainers__card {
  position: relative;
}

.trainers__card-info {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}

.trainers__card:hover .trainers__card-info {
  z-index: 2;
}

.trainers__card:focus-within .trainers__card-info {
  z-index: 2;
}

